package ui.pages

import ui.shared.dataListView
import Requester
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import kotlinx.html.*
import kotlinx.html.dom.append
import kotlinx.html.js.onClickFunction
import kotlinx.html.js.onTouchEndFunction
import kotlinx.html.js.onTouchStartFunction
import misc.TimeLength
import misc.js.Cookie
import misc.js.Media
import misc.js.Swiper.htmlTabs


import misc.js.Swiper.initTabs
import ui.shared.trackCard
import models.Playlist
import models.Profile
import models.Track
import org.w3c.dom.HTMLElement
import org.w3c.fetch.Request


object ProfilePage : InfoViewPage {

    override fun loadPage() {

        document.body?.append {

            GlobalScope.launch {





                val qUid = params.get("uid")


                if (qUid == null)
                    window.location.href = window.location.origin + "spotify/profile/me"


                //awaited requests:
                val user = Requester().getUser(qUid!!)
                user.playlists = Requester().getUsersPlaylists(qUid!!);


                if(Cookie.get("myUid") == user.id){ //if this is my profile...
                    user.topTracks[TimeLength.MONTH] = Requester().getTopTracks(TimeLength.MONTH)
                    user.topTracks[TimeLength.YEAR] = Requester().getTopTracks(TimeLength.YEAR)
                    user.topTracks[TimeLength.ALL_TIME] = Requester().getTopTracks(TimeLength.ALL_TIME)
                }


                // dev log stuff:
                console.info(user)
                // these are already in the user object:
                //console.info(user.playlists)
                //console.info(user.topTracks)

                //user info bar (from superclass)
                infoBar(user, qUid)


                htmlTabs(

                    //tab 1 (playlists)
                    {
                        dataListView<Playlist>("playlists", user.playlists!!) {

                            onClickFunction = { ev ->
                                window.location.href = "${window.location.origin}/spotify/playlist/?pid=${it.id}"
                            }

                            //name
                            span("cardTitle") { +it.name };

                            // num tracks
                            i("cardSubTitle") { +"${it.numTracks} tracks"; }
                            br { style = "clear: both;" }

                            //description
                            span("cardDetail") {
                                b { +"description:" }
                                i { +it.description }
                            }


                        }
                    },

                    //tab 2 (tops month):
                    {
                        dataListView<Track>("top tracks (month):", user.topTracks[TimeLength.ALL_TIME] ?: listOf<Track>() ) {
                            topT ->
                            trackCard(topT)
                        }
                    },
                    //tab 3 (tops year):
                    {
                        dataListView<Track>("top tracks (year):", user.topTracks[TimeLength.YEAR] ?: listOf<Track>() ) {
                                topT ->
                            trackCard(topT)
                        }
                    },
                    //tab 4 (tops all time):
                    {
                        dataListView<Track>("top tracks (all time):", user.topTracks[TimeLength.MONTH] ?: listOf<Track>() ) {
                                topT ->
                            trackCard(topT)
                        }
                    },



                )




                initTabs()


                /*
            //init swiper
            initSwiperHtml()
            initSwiper()
             */





            }

        }
    }




    inline fun TagConsumer<HTMLElement>.infoBar(user: Profile, qUid: String) = div {

            divInfoView(
                imgUrl = user.imageUrl,
                imgTxt = user.displayName,

            ) {



                    li {
                        +"Followers: ${user.followers}"
                    }



                //user id bottom right
                small {
                    style = "text-align: right; flex-grow: 1; float: right; margin-right: 2vw; "
                    +"$qUid"
                }

                br { style = "break: both;" }
            }
        }
    }



    inline fun TagConsumer<HTMLElement>.dataView(user: Profile) {


        div {



        }
    }
