package models

import Requester
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import kotlinx.serialization.json.JsonNames


data class Playlist (
    val collaborative: Boolean,
    val description: String,
    val href: String,
    val id: String,
    val name: String,
    val public: Boolean,
    val uri: String,
    val numTracks: Int,
    val tracksLink :String,
    val imageUri :String

) {


    lateinit var tracksList : List<Track>


    init {



    }

suspend fun initTracks(){
    GlobalScope.launch {
        val ts = requestTracks()
        tracksList = ts;
    }
}
    private suspend fun requestTracks() :List<Track> {
        return Requester().getPlaylistTracks(tracksLink)
    }


    companion object {

        fun listFromDynamic(inListObj:dynamic) :List<Playlist>{
            val playlistsout = mutableListOf<Playlist>()

            for(pl in inListObj.items) {
                val plkt = Playlist.fromDynamic(pl)

                playlistsout.add(plkt)
            }
            //        it -> playlistsout.add(Playlist.fromDynamic(it))

            //playlistsout.forEach { console.info(it) }

            return playlistsout.toList<Playlist>()
        }




        fun fromDynamic(inObj:dynamic) :Playlist{





        return Playlist(
            inObj.collaborative,
            inObj.description,
            inObj.href,
            inObj.id,
            inObj.name,
            inObj.public,
            inObj.uri,
            inObj.tracks.total,
            inObj.tracks.href,
            inObj.images[1]?.url ?: inObj.images[0]?.url ?: ""


        )
        }
    }

}

data class Image (
    val height: Long? = null,
    val url: String,
    val width: Long? = null
)
data class Tracks (
    val href: String,
    val total: Long
)
