package ui.pages

import kotlinx.browser.window
import misc.js.Cookie
object Profile_MePage : Page {
    override fun loadPage() {

        val myUid = Cookie.get("myUid")

        console.log(myUid)

        if (myUid.isNullOrEmpty())
            window.location.href = "${window.location.origin}/spotify/auth"
        else
            window.location.href = "${window.location.origin}/spotify/profile?uid=$myUid"
    }
}