package ui.shared

import kotlinx.html.DIV
import kotlinx.html.br
import kotlinx.html.js.onClickFunction
import kotlinx.html.p
import kotlinx.html.span
import misc.js.Media
import models.Track

fun DIV.trackCard(topT: Track) {
    onClickFunction = { ev ->
        Media.playSoundFromUrl(topT.previewURL)
    }

    span("cardTitle") { +topT.name }

    span("cardSubTitle") { +topT.artists }
}